
import XHeader from 'vux/src/components/x-header/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
name: "myProfit",
data() {
return {
percent2: 0,
strokeColor2: '#fff',
id:'',
rewardList:[],
todaydata:'',
onFetching: false,
bottomCount:10
}

},
components: {
XHeader,XCircle,Group,Cell,LoadMore
},
created() {
this.$axios.post('/api/Myprofit/my_profit','data='+JSON.stringify({page:1}))
.then(res => {
if(res.code==1){
this.rewardList=res.result.profit_list;
this.percent2 = res.result.todaydata
this.todaydata=res.result;
if( this.rewardList==0){
mui.toast("暂无数据")
}
}
})
},
mounted() {

},
methods: {

}
}



